<template>
  <partner-registration-pending
    v-if="
      ticketOffer.status === 'pending' || ticketOffer.status === 'purchased'
    "
    :event="event"
    :person="person"
    :ticketOffer="ticketOffer"
  />
  <partner-registration-surrendered
    v-else-if="ticketOffer.status === 'surrendered'"
    :event="event"
    :person="person"
  />
</template>

<script>
import PartnerRegistrationPending from "@/ticketing/views/PartnerRegistrationPending";
import PartnerRegistrationSurrendered from "@/ticketing/views/PartnerRegistrationSurrendered";
import { mapGetters } from "vuex";

export default {
  name: "PartnerRegistration",
  components: {
    PartnerRegistrationPending,
    PartnerRegistrationSurrendered
  },
  data() {
    return {
      status: {
        pending: "partner-registration"
      }
    };
  },
  computed: {
    ...mapGetters({
      event: "events/event",
      person: "people/person",
      ticketOffer: "ticketOffers/ticketOffer"
    })
  }
};
</script>
