<template>
  <ticket-offer-spaces-full
    v-if="event.soldOut && ticketOffer.status !== 'purchased'"
  />
  <ticket-sales-closed
    v-else-if="!event.ticketSalesActive && ticketOffer.status !== 'purchased'"
  />
  <div v-else>
    <ticket
      v-if="alreadyRegistered"
      :person="person"
      :event="event"
      :ticketOffer="ticketOffer"
    />

    <section v-if="alreadyRegistered" class="section pb-0 hide-print">
      <div class="container">
        <div class="columns is-centered">
          <div class="column is-6 content">
            <h4>Great! You're attending.</h4>
            <p>
              Your ticket is above. Please bring it on the day, either on your
              phone or printed out.
            </p>
            <p>You can use the form below to update any details.</p>
            <p>See you on the day!</p>
          </div>
        </div>
      </div>
    </section>

    <div class="section pt-3">
      <page-content
        v-if="!alreadyRegistered"
        class="pb-0 hide-print"
        :contentId="contentId"
      />

      <section class="section container hide-print">
        <div class="columns is-centered">
          <div class="column is-6">
            <form @submit.prevent="register">
              <b-field
                label="First Name"
                :type="{ 'is-danger': $v.personAnswers.firstName.$error }"
                :message="
                  $v.personAnswers.firstName.$error
                    ? 'Please enter your first name'
                    : ''
                "
              >
                <b-input
                  v-model="personAnswers.firstName"
                  placeholder="Leslie"
                  @blur="$v.personAnswers.firstName.$touch()"
                  type="text"
                  icon="account"
                >
                </b-input>
              </b-field>

              <b-field
                label="Last Name"
                :type="{ 'is-danger': $v.personAnswers.lastName.$error }"
                :message="
                  $v.personAnswers.lastName.$error
                    ? 'Please enter your last name'
                    : ''
                "
              >
                <b-input
                  v-model="personAnswers.lastName"
                  placeholder="Miles"
                  @blur="$v.personAnswers.lastName.$touch()"
                  type="text"
                  icon="account"
                >
                </b-input>
              </b-field>

              <b-field
                label="Email"
                :type="{
                  'is-danger': $v.personAnswers.email.$error || errors.email
                }"
                :message="errors.email ? errors.email : ''"
              >
                <b-input
                  v-model="personAnswers.email"
                  placeholder="francis@example.com"
                  @blur="$v.personAnswers.email.$touch()"
                  type="email"
                  icon="email"
                >
                </b-input>
              </b-field>

              <fieldset>
                <legend class="label">
                  Do you have any Dietary Requirements?
                </legend>
                <div class="checkbox-row">
                  <b-checkbox
                    v-for="requirement in dietaryRequirements"
                    :key="requirement"
                    class="checkbox-column"
                    v-model="personAnswers.data.dietaryRequirements"
                    :native-value="requirement"
                  >
                    {{ requirement }}
                  </b-checkbox>
                </div>
              </fieldset>

              <fieldset>
                <legend class="label">
                  Do you have any Accessibility Requirements?
                </legend>
                <div class="checkbox-row">
                  <b-checkbox
                    v-for="requirement in accessabilityRequirements"
                    :key="requirement"
                    class="checkbox-column"
                    v-model="personAnswers.data.accessabilityRequirements"
                    :native-value="requirement"
                  >
                    {{ requirement }}
                  </b-checkbox>
                </div>
              </fieldset>

              <div
                v-if="alreadyRegistered"
                class="has-text-right has-text-success is-success already-registered"
              >
                <b-icon icon="check-circle" type="is-success" size="is-small">
                </b-icon>
                You're attending
              </div>

              <div class="actions-row buttons is-pulled-right">
                <button
                  v-if="ticketOffer.id"
                  @click.prevent="rejectModalActive = true"
                  class="button is-medium"
                >
                  {{ rejectText }}
                </button>
                <input
                  class="button is-medium is-primary"
                  type="submit"
                  :value="acceptText"
                />
              </div>
            </form>
          </div>
        </div>
      </section>

      <b-modal :active.sync="rejectModalActive">
        <div class="modal-card center-modal">
          <header class="modal-card-head">
            <p class="modal-card-title">Are You Sure</p>
            <button
              class="delete"
              @click.prevent="rejectModalActive = false"
              aria-label="close"
            ></button>
          </header>
          <section class="modal-card-body">
            <p>
              The person who invited you will be notified so they can invite
              someone else.
            </p>
          </section>
          <footer class="modal-card-foot">
            <button
              @click.prevent="rejectModalActive = false"
              class="button is-primary"
            >
              cancel
            </button>
            <button @click.prevent="rejectTicket()" class="button">
              Give Up Ticket
            </button>
          </footer>
        </div>
      </b-modal>
    </div>
  </div>
</template>

<script>
import TicketOfferSpacesFull from "@/ticketing/views/TicketOfferSpacesFull";
import TicketSalesClosed from "@/ticketing/views/TicketSalesClosed";
import Ticket from "@/components/Ticket";
import PageContent from "@/ticketing/components/PageContent";
import { required, email } from "vuelidate/lib/validators";
import { markdownSubstitutionsMixin } from "@/mixins/markdownSubstitutionsMixin";
import { mapGetters } from "vuex";

export default {
  name: "PartnerRegistrationPending",
  components: {
    TicketOfferSpacesFull,
    TicketSalesClosed,
    PageContent,
    Ticket
  },
  validations: {
    personAnswers: {
      firstName: { required },
      lastName: { required },
      email: { required, email }
    }
  },
  beforeMount() {
    if (this.alreadyRegistered) {
      this.$store.dispatch("layouts/setBannerVisibility", false);
    }
  },
  mixins: [markdownSubstitutionsMixin],
  data() {
    return {
      personAnswers: this.person,
      rejectModalActive: false,
      contentName: "partner_registration_page_content",
      errors: {}
    };
  },
  props: {
    event: {
      type: Object,
      required: true
    },
    person: {
      type: Object,
      required: true
    },
    ticketOffer: {
      type: Object,
      required: true
    }
  },
  methods: {
    async register() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        await this.$store.dispatch("people/createPerson", {
          person: {
            firstName: this.personAnswers.firstName,
            lastName: this.personAnswers.lastName,
            email: this.personAnswers.email
          },
          profile: this.personAnswers.data
        });

        if (!this.alreadyRegistered) {
          await this.$store.dispatch("ticketOffers/registerPartner");
          this.$snackbar.open({
            message: `Successfully Registered: Thank you`,
            position: "is-bottom-left"
          });
          this.$router.push({
            name: "partner-registration",
            params: { id: this.$store.getters["ticketOffers/ticketOffer"].id }
          });
        }
      }
    },

    rejectTicket() {
      this.$store.dispatch(
        "ticketOffers/rejectTicketOffer",
        this.ticketOffer.id
      );
    }
  },
  computed: {
    ...mapGetters(["dietaryRequirements", "accessabilityRequirements"]),
    ...mapGetters({
      organisation: "organisations/organisation"
    }),
    contentId() {
      return this.event.pageContent.find(c => c[0] === this.contentName)[1];
    },
    alreadyRegistered() {
      return this.ticketOffer.status === "purchased";
    },
    acceptText() {
      return this.alreadyRegistered ? "Update Details" : "Yes, I'm Coming";
    },
    rejectText() {
      return this.alreadyRegistered
        ? "I can't make it anymore"
        : "No, I'm Not Coming";
    }
  }
};
</script>

<style scoped>
.rsvp-radios {
  margin: 1rem;
}

.actions-row {
  margin-top: 1rem;
}

.already-registered {
  margin-bottom: -0.7rem;
}
</style>
